import React from "react"

const Icon = (props) => {
  let count = []

  if (props.count) {
    if (props.prev) {
      count = (
        <span className={`icon-count-wrapper` + ((props.prev > props.count) ? ` reverse` : ``)}>
          <span className={`icon-count prev-count`} key={props.prev}>{props.prev}</span>
          <span className={`icon-count curr-count`} key={props.count}>{props.count}</span>
        </span>
      )
    }
    else {
      count = (
        <span className={`icon-count-wrapper`}>
          <span className={`icon-count`}>{props.count}</span>
        </span>
      )
    }
  }

  return (
    <span className={`icon-wrapper icon-wrapper-` + props.type}>
      <span className={`icon-wrapper-outer icon-wrapper-outer-` + props.type} onClick={props.callback}>

        <span className={`icon-wrapper-inner icon-wrapper-inner-` + props.type}>
          {props.icon}
        </span>

        {count}

      </span>
    </span>
  )
}

export default Icon;
